	module.exports = {"data":{"markdownRemark":{"html":"<p>Bayes Classifier is based on applying Bayes Theorem to update its belief on the probability of an event occurring. </p>\n<p><img src=\"https://github.com/Cheungo/bayes_theorem_image/blob/master/CodeCogsEqn.gif?raw=true\" alt=\"Bayes Theorem\"></p>\n<p>Bayes Theorem allows you to compute the probability of B given A, provided you have probabilities for A given B, A, and B.</p>\n<p>Bayes Classifier models uncertainty by keeping probabilities for each of the possible scenarios. As more information becomes available, the probabilities are updated to more accurately reflect what is now known about the given situation. </p>\n<h4>Suggested Reading:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li><a href=\"https://monkeylearn.com/blog/practical-explanation-naive-bayes-classifier/\">https://monkeylearn.com/blog/practical-explanation-naive-bayes-classifier/</a></li>\n<li><a href=\"https://en.wikipedia.org/wiki/Naive_Bayes_classifier\">https://en.wikipedia.org/wiki/Naive<em>Bayes</em>classifier</a></li>\n<li><a href=\"https://youtu.be/XcwH9JGfZOU\">https://youtu.be/XcwH9JGfZOU</a> (Part 1)</li>\n<li><a href=\"https://youtu.be/k2diLn5Nqbs\">https://youtu.be/k2diLn5Nqbs</a> (Part 2)</li>\n</ul>","fields":{"slug":"/machine-learning/bayes-classifier/"},"frontmatter":{"title":"Bayes Classifier"}}},"pathContext":{"slug":"/machine-learning/bayes-classifier/"}}

